<template>
  <div id="app">
        <img src="../assets/logo.png" width="100px" height="100px">
    <h1 class="mt-3 title">맥 짚는 AI</h1>
    <div class="container">

      <!-- Comment input -->
      <section>
        <input v-model="pageNum"></input>
        <button @click = "movePage"> 이동 </button>
      </section>

      <section class="container">
          <div class="row">
             <div class="offset-md-1 col-md-10 mt-3">

               <!-- Comment title -->
               <ul class="list-group justify-content-center">
                 <li class="row list-group-item mt-2 col-xs-1 listTitle">
                   <div class="row align-items-center">
                     <span class="col-sm-2">News_num</span>
                     <span class="col-sm-7">내용</span>
                     <span class="col-sm-1">ab_news</span>
                     <span class="col-sm-1">ab_local</span>
                     <span class="col-sm-1">삭제</span>
                   </div>
                 </li>
               </ul>

               <!-- Comment list -->
               <ul class="list-group justify-content-center">
                <template v-for="(comment, index) in commentList">
                 <li class="row list-group-item border mt-2 col-xs-1">
                    <div class="row align-items-center">

                      <span class="col-sm-2" >
                        <label> {{ comment.news_num }}</label>
                      </span>
                      <!-- Comment content -->
                      <span class="col-sm-7">
                         <h5>{{ comment.comment_context }}</h5>
                      </span>

                      <!-- Comment label -->
                      <span class="col-sm-1" >
                        <button class="btn btn-primary" v-if="comment.label_news == 1" @click="editCommentNews(comment)">
                          긍정
                        </button>
                        <button class="btn btn-danger" v-else @click="editCommentNews(comment)">
                          부정
                        </button>
                      </span>

                      <span class="col-sm-1" >
                        <button class="btn btn-primary" v-if="comment.label_local == 1" @click="editCommentLocal(comment)">
                          긍정
                        </button>
                        <button class="btn btn-danger" v-else @click="editCommentLocal(comment)">
                          부정
                        </button>
                      </span>

                      <!-- Comment delete button -->
                      <span class="col-sm-1 delete" @click="deleteComment(comment)">X</span>
                    </div>
                  </li>
                </template>

                <!-- paging button -->
                <ul id="paging">
                  <li v-if="thisPage != 1" @click="getComment(1)">
                    처음
                  </li>
                  <li v-for="i in pageList" @click="getComment(i)">
                    <div class="thisPageClass" v-if="i === thisPage">{{i}}</div>
                    <div v-else>{{i}}</div>
                  </li>
                  <li v-model="finalPage" @click="getComment(finalPage)">
                    마지막
                  </li>
                </ul>


                </ul>
             </div>
          </div>
       </section>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import BootstrapVue from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import Server from "../services/Server.js";

export default {
  name: 'app',
  data () {
    return {
      rootPath: window.location.href,
      inputField: '',
      commentList: [],
      pageList: [],
      thisPage: 1,
      finalPage: 0,
      pageNum: 1
    }
  },
  created() {
    this.getComment(1);
  },
  methods: {
    // get comments
    getComment(page) {
      var self = this;
      var form = new FormData();
      this.thisPage = page;
      this.getPages(this.thisPage);
      form.append("page", this.thisPage);

      Server(this.rootPath).post("api/get/comments_page", form)
        .then(res => {
          self.commentList = res.data;
        });
    },
    // get page
    getPages(page) {
      var self = this;
      var form = new FormData();
      this.thisPage = page;
      form.append("page", this.thisPage);

      Server(this.rootPath).post("api/get/page_count", form)
        .then(res => {
          self.pageList = res.data.page_list;
          self.finalPage = res.data.final_page;
        });
    },
    // add comments
    addComment(){
      var self = this;
      var form = new FormData();
      form.append("context",this.inputField);

      Server(this.rootPath).post("api/insert/comments", form)
        .then(res => {
          self.commentList
            .unshift({
              num: self.commentList.length,
              context: self.inputField,
              label: res.data
            });
          self.inputField='';
        });
   },
   // delete comments
   deleteComment: function(comment) {
     var index = this.commentList.indexOf(comment);
     var self = this;
     var form = new FormData();
     form.append("num",comment.comment_num);

     Server(this.rootPath).post("api/del/comments", form)
       .then(res => {
         self.commentList.splice(index,1);
       });
   },
   //edit Comment Logistic
   editCommentNews: function(comment) {
     var self = this;
     var form = new FormData();
     form.append("num", comment.comment_num);
     form.append("label_news", comment.label_news);

     Server(this.rootPath).post("api/edit/labelnews", form)
       .then(res => {
         if(comment.label_news == 0){
           comment.label_news = 1;
         }else {
           comment.label_news = 0;
         }
       });
   },

   editCommentLocal: function(comment) {
     var self = this;
     var form = new FormData();
     form.append("num", comment.comment_num);
     form.append("label_local", comment.label_local);

     Server(this.rootPath).post("api/edit/labellocal", form)
       .then(res => {
         if(comment.label_local == 0){
           comment.label_local = 1;
         }else {
           comment.label_local = 0;
         }
       });
   },
   movePage: function() {
     this.thisPage = this.pageNum;
     getComment(this.thisPage);
   },
   //toggle comments
   toggle: function(comment) {
      comment.complete = !comment.complete;
   }
 }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  margin-top: 60px;
}

#paging {
  margin-top: 10px;
  margin-bottom: 10px;
}

.title{
  font-size: 4rem;
}

.textInput{
border: none;
border-bottom: solid 2px #000000;
}

h5 {
   word-break:break-all;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.delete {
   color: pink;
   cursor: pointer;
}

.delete:hover {
   color: red;
}

.thisPageClass {
 color : blue;
 font-size: 1.3rem;
 font-weight: bold;
}

.listTitle {
  background-color: #EBFBFF;
}
</style>
